.playlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  color: darkslategray;
  margin: 0 1rem;
  padding: 1rem;
}

.playlistName {
  border: none;
  border-bottom: 0.05rem solid darkslategray;
}

.Button {
  border-radius: 1rem;
  background-color: black;
  color: white;
  padding: 0.5rem 1rem;
  width: 50%;
  display: inline-block;
  margin: 0.5rem 0;
}

@media only screen and (min-width: 481px) {
  .playlist {
    width: 50%;
  }

}
